.acntValue {
    color: #eaf191;
}

.control-span {
    display: inline-block;
    width: auto;
    vertical-align: middle;
    margin-top: 2px;
    border: 0;
    outline: none;
    background-color: inherit;
    cursor: default;
    margin-right: 5px;
}
input.control-span {
    // width: 40px;
}

//-- down div tag
.navbar-nav-topdiv {
    float: left;
    margin: 0;
}

.nav-topdiv {
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
}

div.div_top_child_d_elm ul.nav-topdiv ul.dropdown-menu {
    border: 0.5px solid #676161;
    top: 10px !important;
    max-height: unset;
}

.nav-topdiv {
    cursor: pointer;
}

.navbar-nav-topdiv > li {
    vertical-align: middle;
    padding-top: 0;
}

div.div_top_elm .form-control:focus {
    color: rgb(232, 232, 232);
    background-color: #141313;
}

.navbar-nav-topdiv > li {
    float: left;
}

.nav-topdiv > li,
.nav-topdiv > li > a {
    display: block;
    position: relative;
    margin-bottom: 8px;
}

.nav-topdiv > li > a {
    font-size: 12px;
    font-weight: 600;
    color: #e2dede;
    height: 24px;
    height: 30px;
    letter-spacing: 0.09px;
    float: left;
}

.navbar-nav-topdiv > li > a {
    padding-top: 0;
    padding-bottom: 0;
}

.navbar-nav-topdiv > li > a {
    padding-top: 5px;
}

.navbar-nav-topdiv > li > a {
    padding-bottom: 10px;
    line-height: 20px;
}

.nav-topdiv > li > a {
    padding: 5px 10px;
}

.nav-topdiv > li,
.nav-topdiv > li > a {
    display: block;
    position: relative;
}

.nav-topdiv > li > a:hover {
    color: yellow;
}

.nav-topdiv > li:after {
    position: relative;
    width: 2px;
    height: 20px;
    bottom: 0;
    // background-color: #444;
    float: left;
    content: '';
    margin: 5px 2px 2px;
}

a {
    background: 0 0;
    color: #428bca;
    text-decoration: none;
}

// -- Cho menu child
.dropdown-menu-parent {
    padding-top: 0;
    padding-bottom: 0;
    border: none;
    margin-top: -3px !important;
}

.navbar-nav-topdiv > li > .dropdown-menu {
    border-top-right-radius: 0;
    border-top-left-radius: 0;
}

.dropdown-menu-parent > .dropdown-menu {
    display: inline-block;
}

.dropdown-menu {
    background-color: #323232;
    color: #fff;
    margin: 0;
    box-sizing: border-box;
    border-radius: 0;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    float: left;
    padding: 5px 0;
    list-style: none;
    font-size: 14px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
    background-clip: padding-box;
}

.dropdown-menu .divider-top {
    background-color: #545353;
    height: 1px;
    padding-bottom: 1px;
    padding-top: 0;
    margin: 4px 8px;
    overflow: hidden;
    padding: 0 10px;
}

.menu-item-element {
    position: relative;
    padding: 6px 16px;
}

.dropdown-menu li.menu-item-element > a {
    line-height: 16px;
    font-size: 10.5pt;
    color: #fff;
    clear: both;
    display: block;
    // padding: 3px 8px;
    font-weight: 400;
    white-space: nowrap;
}
.dropdown-menu li.menu-item-element:hover {
    background-color: #8e5206;
}
li.menu-item-element input {
    color: whitesmoke !important;
}
.load-wrapp {
    float: left;
    width: 84px;
}

.serverTime {
    font-size: 15px;
    font-family: monospace;
}

.load-wrapp p {
    padding: 0 0 20px;
}
.load-wrapp:last-child {
    margin-right: 0;
}

.letter {
    float: left;
    color: red;
    font-weight: 600;
}
.load-animation .letter {
    animation-name: loadingF;
    animation-duration: 1.6s;
    animation-iteration-count: infinite;
    animation-direction: linear;
}
@keyframes loadingF {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.l-1 {
    animation-delay: 0.48s;
}
.l-2 {
    animation-delay: 0.6s;
}
.l-3 {
    animation-delay: 0.72s;
}
.l-4 {
    animation-delay: 0.84s;
}
.l-5 {
    animation-delay: 0.96s;
}
.l-6 {
    animation-delay: 1.08s;
}
.l-7 {
    animation-delay: 1.2s;
}
.l-8 {
    animation-delay: 1.32s;
}
.l-9 {
    animation-delay: 1.44s;
}
.l-10 {
    animation-delay: 1.56s;
}
.l-11 {
    animation-delay: 1.68s;
}
.l-12 {
    animation-delay: 1.8s;
}
