  // option ẩn hiện cột, chọn time, type của div bottom và bảng điện
  .popover-search {
    background: #403d4f;
    color: #d3d3d3;
    padding: 12px;
    td {
      font-size: 12px;
    }
    td input[type="radio"] {
      position: absolute;
      visibility: hidden;
    }

    td label {
      display: block;
      position: relative;
      // padding: 15px 15px 20px 25px;
      height: 30px;
      z-index: 9;
      cursor: pointer;
      -webkit-transition: all 0.25s linear;
      color: #aaaaaa;
      white-space: nowrap;
    }

    td:hover label {
      color: #d3d3d3;
    }

    td .check {
      display: block;
      border: 2px solid #aaaaaa;
      border-radius: 100%;
      height: 16px;
      width: 16px;
      margin-top: -27px;
      z-index: 5;
      transition: border 0.25s linear;
      -webkit-transition: border 0.25s linear;
    }

    td:hover .check {
      border: 3px solid #d3d3d3;
    }

    td .check::before {
      display: block;
      content: "";
      border-radius: 100%;
      height: 8px;
      width: 8px;
      margin: 2px;
      transition: background 0.25s linear;
      -webkit-transition: background 0.25s linear;
    }

    input[type="radio"]:checked ~ .check {
      border: 2px solid #0dff92;
    }

    input[type="radio"]:checked ~ .check::before {
      background: #0dff92;
    }

    input[type="radio"]:checked ~ label {
      color: #0dff92;
    }
  }
  .popover-search .styled-checkbox:checked + label:before,
  .styled-checkbox:checked + label:before {
    background: #17a290;
    border-color: #17a290;
  }
  .popover-search .styled-checkbox:checked + label:after,
  .styled-checkbox:checked + label:after {
    background: #36324a;
    box-shadow: 2px 0 0 #36324a, 4px 0 0 #36324a, 4px -2px 0 #36324a, 4px -4px 0 #36324a, 4px -6px 0 #36324a,
      4px -8px 0 #36324a;
  }
  .popover-search .styled-checkbox:disabled + label:before,
  .styled-checkbox:disabled + label:before {
    box-shadow: none;
    background: #ddd;
    border-color: #ddd;
  }

