    background: #FFFFFF;
    box-shadow: 2px 0 0 #FFFFFF, 4px 0 0 #FFFFFF, 4px -2px 0 #FFFFFF, 4px -4px 0 #FFFFFF, 4px -6px 0 #FFFFFF,
      4px -8px 0 #FFFFFF;
  }
  .popover-search .styled-checkbox:disabled + label:before,
  .styled-checkbox:disabled + label:before {
    box-shadow: none;
    background: #ddd;
    border-color: #ddd;
  }
  .popover-search input[type="checkbox"]:checked ~ label {
    color: #262F5A;
  }

  ::placeholder {
    color: #9e9db6;
    opacity: 1; /* Firefox */
  }

  :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #9e9db6;
  }

  ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: #9e9db6;
  }

  // type date
  /* Removes the clear button from date inputs */
  input[type="date"]::-webkit-clear-button {
    display: none;
  }

  /* Removes the spin button */
  input[type="date"]::-webkit-inner-spin-button {
    display: none;
  }

  /* Always display the drop down caret */
  input[type="date"]::-webkit-calendar-picker-indicator {
    color: #1C172F;
  }

  /* A few custom styles for date inputs */
  input[type="date"] {
    appearance: none;
    -webkit-appearance: none;
    color: #1C172F;
    font-size: 18px;
    border: 1px solid #E1E1E1;
    background: #E1E1E1;
    border-radius: 4px;
    padding: 5px;
    display: inline-block !important;
    visibility: visible !important;
    width: 100%;
  }

  input[type="date"]:focus {
    color: #1C172F;
    // border: none;
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }

  .loading {
    color: black;
  }
  .btn {
    white-space: nowrap;
  }
  .table_priceboard_small td, .table_priceboard_small th {
    border: 0.5px solid #D7D7D7;
  }
}