.login-container {
  display: flex;
  align-items: center;
  height: 100%;
}

.login-box .card {
  padding: 15px;
}

.login-box .form-group {
  margin-bottom: 5px;
}

.login-box .form-control {
  border-radius: unset !important;
}

.login-box label {
  font-weight: 400;
}

.auth-sep {
  margin-top: 22px;
  margin-bottom: 24px;
  line-height: 20px;
  text-align: center;
  display: block;
  position: relative;
}

.auth-sep > span {
  display: table-cell;
  width: 30%;
  white-space: nowrap;
  color: #555;
  font-size: 12px;
}

.auth-sep > span > span {
  margin-top: -11px;
  display: block;
  font-weight: 300;
}

.auth-sep:after,
.auth-sep:before {
  content: "";
  height: 1px;
  width: 100%;
  display: table-cell;
}

// ------- start project--------------

.loginbox-title {
  position: relative;
  text-align: center;
  width: 100%;
  margin-bottom: 20px;
}

.login_log {
  height: 48px;
  // width: 264px;
}

.lowercase {
  text-transform: lowercase;
}

.loginPrepend {
  width: 40px;
}

.loginPrepend i {
  margin: 0 auto;
  font-size: 18px;
  margin-bottom: 0 !important;
