.modal-header .close > span {
  color: #000;
}

.pulsate {
  border: 1px solid blue;
  border-radius: 100px;
  height: 40px;
  width: 40px;
  position: absolute;
  top: -15px;
  right: -14px;
  -webkit-animation: 5s ease-out infinite pulsate;
  animation: 5s ease-out infinite pulsate;
  opacity: 0;
}

@-webkit-keyframes pulsate {
  30% {
    -webkit-transform: scale(0.1, 0.1);
    opacity: 0;
  }

  35% {
    opacity: 1;
  }

  40% {
    -webkit-transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

@-moz-keyframes pulsate {
  30% {
    -moz-transform: scale(0.1, 0.1);
    opacity: 0;
  }

  35% {
    opacity: 1;
  }

  40% {
    -moz-transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

@-o-keyframes pulsate {
  30% {
    -o-transform: scale(0.1, 0.1);
    opacity: 0;
  }

  35% {
    opacity: 1;
  }

  40% {
    -o-transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

@keyframes pulsate {
  30% {
    transform: scale(0.1, 0.1);
    opacity: 0;
  }

  35% {
    opacity: 1;
  }

  40% {
    transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

.languageFlag {
  width: 40px;
  font-size: 18px;
  padding: inherit;
}

/////////////////////////////////////////////
.input-group {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
  align-items: stretch;
  width: 100%;
}

.input-group .input-group-text {
  padding: 0.25rem 0.2rem;
  font-size: 11px;
  border-radius: 0 !important;
}

.help-block {
  font-size: 10px;
  margin-top: 5px;
}

.card-block {
  padding: 10px;
}
.modal-dialog.modal-login {
  width: 350px;
  .modal-body {
    padding: 0;
  }
  .card {
    margin-top: 0;
    margin-bottom: 0;
    background-color: #36324a;
  }
  .card-body {
    padding: 24px;
  }
  input:-internal-autofill-selected {
    background-color: #36324a !important;
    background-image: none !important;
    color: white !important;
  }
}
.input-group-text.input-group-text-custom {
  background-color: inherit;
  border: 0;
  color: inherit;
}
.form-control.input-login {
  background-color: inherit;
  border: 0;
}
.input-group.input-group-login {
  border: 1px solid #615885;
  border-radius: 2px;
  padding: 2px 8px;
  option {
    background-color: inherit;
  }
}
