@import './_vars';
@import '_mixins';

.invisible {
    opacity: 0;
}

.view-enter {
    @include smooth-fade-in-up(0s, 0.5s, ease-in);
    z-index: 1;
}

// fix form inline
.form-inline > * {
    margin: 0px 3px;
}

.chartjs-render-monitor {
    display: block;
    height: 600px;
}

// fix
.panel-heading {
    z-index: 1 !important;
}

