/*---------------------------------------------------
    LESS Elements 0.9
  ---------------------------------------------------
    A set of useful LESS mixins
    More info at: http://lesselements.com
  ---------------------------------------------------*/
/*
    Components: navbar, logo and content header
-------------------------------------------------
*/
body section div header {
  position: relative;
  max-height: 100px;
  z-index: 1030;
}
body section div header .navbar {
  height: 50px;
  margin-bottom: 0;
  margin-left: 220px;
}
body section div header .navbar .sidebar-toggle {
  float: left;
  padding: 9px 5px;
  margin-top: 8px;
  margin-right: 0;
  margin-bottom: 8px;
  margin-left: 5px;
  background-color: transparent;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 0 !important;
}
body section div header .navbar .sidebar-toggle:hover .icon-bar {
  background: #f6f6f6;
}
body section div header .navbar .sidebar-toggle .icon-bar {
  display: block;
  width: 22px;
  height: 2px;
  border-radius: 4px;
}
body section div header .navbar .sidebar-toggle .icon-bar + .icon-bar {
  margin-top: 4px;
}
body section div header .navbar .nav > li.user > a {
  font-weight: bold;
}
body section div header .navbar .nav > li.user > a > .fa,
body section div header .navbar .nav > li.user > a > .glyphicon,
body section div header .navbar .nav > li.user > a > .ion {
  margin-right: 5px;
}
body section div header .navbar .nav > li > a > .label {
  border-radius: 50%;
  position: absolute;
  top: 7px;
  right: 2px;
  font-size: 10px;
  font-weight: normal;
  width: 15px;
  height: 15px;
  line-height: 1.0em;
  text-align: center;
  padding: 2px;
}
body section div header .navbar .nav > li > a:hover > .label {
  top: 3px;
}
body section div header > a.logo {
  float: left;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  padding: 0 10px;
  width: 220px;
  font-family: 'Kaushan Script', cursive;
  font-weight: 500;
  height: 50px;
  display: block;
}
body section div header .logo .icon {
  margin-right: 10px;
}
.right-side > .content-header {
  position: relative;
  padding: 15px;
}
.right-side > .content-header > h1 {
  margin: 0;
  font-size: 24px;
}
.right-side > .content-header > h1 > small {
  font-size: 15px;
  display: inline-block;
  padding-left: 4px;
  font-weight: 300;
}
.breadcrumb {
  background-color: #fff;
}
.breadcrumb a {
  color: #000;
}
.right-side > .content-header > .breadcrumb {
  float: right;
  background: transparent;
  margin-top: 0px;
  margin-bottom: 0;
  font-size: 12px;
  padding: 7px 5px;
  position: absolute;
  top: 15px;
  right: 10px;
  border-radius: 2px;
}
.breadcrumb-alt {
  overflow: hidden;
  list-style: none;
  margin: 0px 0px 20px;
  padding: 0px;
  width: 100%;
}
.breadcrumb-alt > li {
float: left;
margin: 0px 25px 0px 0px;
}
.breadcrumb-alt > li > a {
position: relative;
float: left;
background-color: rgb(238, 238, 238);
color: rgb(118, 118, 118);
font-size: 12px;
padding: 10px;
}
.breadcrumb-alt > li > a::before {
position: absolute;
top: 50%;
left: -1em;
margin-top: -1.6em;
border-color: rgb(238, 238, 238) rgb(238, 238, 238) rgb(238, 238, 238) rgba(0, 0, 0, 0);
border-style: solid;
border-width: 1.5em 0px 1.7em 1em;
content: '';
}
.breadcrumb-alt > li > a::after {
position: absolute;
top: 50%;
right: -1em;
margin-top: -1.5em;
border-bottom: 1.5em solid rgba(0, 0, 0, 0);
border-left: 1em solid rgb(238, 238, 238);
border-top: 1.5em solid rgba(0, 0, 0, 0);
content: '';
}


.right-side > .content-header > .breadcrumb > li > a {
  color: #444;
  text-decoration: none;
}
.right-side > .content-header > .breadcrumb > li > a > .fa,
.right-side > .content-header > .breadcrumb > li > a > .glyphicon,
.right-side > .content-header > .breadcrumb > li > a > .ion {
  margin-right: 5px;
}
.right-side > .content-header > .breadcrumb > li + li:before {
  content: '>\A0';
}

@media screen and (max-width: 767px) {
  .right-side > .content-header > .breadcrumb {
    position: relative;
    margin-top: 5px;
    top: 0;
    right: 0;
    float: none;
    background: #efefef;
  }
}
@media (max-width: 767px) {
  .navbar .navbar-nav > li {
    float: left;
  }
  .navbar-nav {
    margin: 0;
    float: left;
  }
  .navbar-nav > li > a {
    padding-top: 15px;
    padding-bottom: 15px;
    line-height: 20px;
  }
  .navbar .navbar-right {
    float: right;
  }
}

