.timeline-messages, .msg-time-chat , .timeline-messages .msg-in, .timeline-messages .msg-out {
    position: relative;
    border-radius: 2px;
}

.timeline-heading {
  padding:8px 15px;
}
.timeline-messages .msg-in  .arrow {
    /*border-right: 8px solid #F4F4F4 !important;*/
}
.timeline-messages .msg-in .arrow {
    border-bottom: 8px solid transparent;
    border-top: 8px solid transparent;
    display: block;
    height: 0;
    left: -8px;
    position: absolute;
    top: 13px;
    width: 0;
}

.timeline-messages .msg-out  .arrow {
    /*border-right: 8px solid #41cac0 !important;*/
}
.timeline-messages .msg-out .arrow {
    border-bottom: 8px solid transparent;
    border-top: 8px solid transparent;
    display: block;
    height: 0;
    left: -8px;
    position: absolute;
    top: 13px;
    width: 0;
}

.msg-time-chat:first-child:before {
    margin-top: 16px;
}
.msg-time-chat:before {
    background:#CCCCCC;
    border: 2px solid #FAFAFA;
    border-radius: 100px;
    -moz-border-radius: 100px;
    -webkit-border-radius: 100px;
    height: 14px;
    margin: 23px 0 0 -6px;
    width: 14px;
}
.msg-time-chat:hover:before {
    background: #41cac0;
}
.msg-time-chat:first-child {
    padding-top: 0;
}
.message-img-in {
    float: left;
    margin-right: 10px;
    overflow: hidden;
}
.message-img-in img {
    display: block;
    height: 50px;
    width: 50px;
    border: 1px solid #eee;
}

.message-img-out {
    float: right;
    margin-left: 10px;
    overflow: hidden;
}
.message-img-out img {
    display: block;
    height: 50px;
    width: 50px;
    border: 1px solid #eee;
}
.message-body-in {
    /*margin-left: 80px;*/
}
.msg-time-chat .msg-in .text {
    /* border: 1px solid #e3e6ed; */
    padding: 10px;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    background-color: #F4f4f4;
    display: inline-block;
}

.message-body-out {
    /*margin-right: 80px;*/
}

.msg-time-chat .msg-out .text {
    /* border: 1px solid #e3e6ed; */
    padding: 10px;
    border-radius: 4px;

    background-color: #F1F5FD;
}
.msg-time-chat p {
    margin: 0;
}
.msg-time-chat .attribution {
    font-size: 11px;
    margin: 0px 0 5px;
}
.msg-time-chat {
    overflow: hidden;
    padding:8px 0;
}

.msg-in a, .msg-in a:hover{
    color: #acacac;
    text-decoration: none;
    border-radius: 4px;
    -webkit-border-radius: 4px;
