.form-title {
    border-bottom: 1.2px dashed #008b8b;
    font-size: 11pt;
    font-weight: 500;
    color: #00f;
    margin-bottom: 10px;
    white-space: nowrap;
}
.modal-dialog.modal-xxl {
    max-width: 75%;
    min-width: 1210px;
    margin: 0.5rem auto;
}
.modal.fade .modal-xxl {
    .card {
        // border: 1px solid rgba(0,0,0,.125);
        border-radius: .25rem;
    }
    .asset {
        margin-bottom: 5px;
        margin-top: 0;
        font-size: 12px;
    }

    .font10 #accountInfo_acntNo,.font10 #assetInfo_acntNo {
        font-size: inherit!important;
    }
    .form-group {
        margin-bottom: 2px;
    }
    .input-group-sm .form-control {
        height: 20px;
    }
    .input-group-sm>.custom-select, .input-group-sm>.form-control, .input-group-sm>.input-group-append>.btn, .input-group-sm>.input-group-append>.input-group-text, .input-group-sm>.input-group-prepend>.btn, .input-group-sm>.input-group-prepend>.input-group-text {
        padding: 2px;
        // font-size: .875rem;
        line-height: 1.1;
        border-radius: .2rem;
    }
    .card-header.row {
        margin: 0;

    }
    .card-header {
        padding: 5px 10px;
    }
    select {
        background-color: inherit;
    }
    .form-group .form-control {
        font-size: inherit;
    }

}
.table_sticky_own thead tr:first-child th, .table_sticky_invest thead tr:first-child th {
    position: -webkit-sticky;
    position: sticky;
    // background-color: skyblue;
    top: -1px;
}
.table_sticky_own thead tr:nth-child(2) th, .table_sticky_invest thead tr:nth-child(2) th {
    position: -webkit-sticky;
    position: sticky;
    // background-color: skyblue;
    top: 18.8px;
}
.table_sticky_own tfoot td, .table_sticky_invest tfoot td {
    position: -webkit-sticky;
    position: sticky;
    // background: #ccc;
    bottom: 0px;
}
.table_sticky_own tbody td:first-child, .table_sticky_invest tbody td:first-child {
    position: -webkit-sticky;
    position: sticky;
    left: -1px;
    // background: #ccc;
}
.table_sticky_own tbody td:nth-child(2) {
    position: -webkit-sticky;
    position: sticky;
    left: 78px;
    background: #ccc;
}
.table_sticky_own thead tr:first-child th:first-child, .table_sticky_invest thead tr:first-child th:first-child {
    position: -webkit-sticky;
    position: sticky;
    left: -1px;
    z-index: 2;
}
.table_sticky_own thead tr:first-child th:nth-child(2) {
    position: -webkit-sticky;
    position: sticky;
    left: 78px;
    z-index: 2;
}
.table_sticky_invest tbody td:nth-child(2) {
    position: -webkit-sticky;
    position: sticky;
    left: 56px;
    // background: #ccc;
}
.table_sticky_invest thead tr:first-child th:nth-child(2) {
    position: -webkit-sticky;
    position: sticky;
    left: 56px;
    z-index: 2;
}
.table_sticky_margin {
     thead tr:first-child th {
        position: -webkit-sticky;
        position: sticky;
        // background-color: skyblue;
        top: -1px;
    }

     tbody td:first-child {
        position: -webkit-sticky;
        position: sticky;
        left: 0;
        // background: #59605f;
    }

    thead tr:first-child th:first-child {
        position: -webkit-sticky;
        position: sticky;
        left: 0;
        z-index: 2;
    }

}
