.public {
    position: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    margin: 0px;
    width: 100%;
    height: 100%;
    overflow-y: hidden;
    background-color: #222;
    font-size: 13px;
    // font-family: Roboto,sans-serif;
    
    .priceBoardTitle {
        margin: 10px 0 0 5px;
        position: absolute;
    }
    .angleClass {
        height: 14px;
        text-align: center;
        background: #222;
        padding: 0;
        min-width: 1154px;
        display: none;
    }
    .tabClass {
        border-top: none !important;
        border-left: none !important;
        border-right: none !important;
        background-color: #222;
        border: 0.5px solid #409047;
        color: white !important;
    }
    #div_notify_content {
        position: relative;
        overflow: auto;
        max-height: 250px;
        padding-bottom: 15px;
    }
    .card-header {
        background: #344154;
        color: #fff;
        line-height: 10px;
        border-radius: 0;
        border: none;
    }
    .cardHeaderPriceBoard {
        position: fixed;
        top: 0;
        width: 100%;
        height: 32px;
        z-index: 1;
        background-color: #181818;
        line-height: 10px;
        left: 0;
        right: 0;
        transform: translate3d(0, 0, 0);
        -webkit-transform: translate3d(0, 0, 0);
    }
    .card-header a {
        color: #fff;
    }
    .w-100p {
        width: 100%;
    }
    .nav-tabs .nav-item .nav-link {
        color: rgba(36,45,58,.8);
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
    .nav-tabs .nav-item .nav-link.active {
        cursor: unset;
    }
    .divPriceBoardPublic {
        margin-top: 200px;
        height: calc(100vh - 210px);
        will-change: tranform;
        width: 100%;
        padding: 60px 0 20px;
        display: block;
        overflow-x: auto;
        // height: calc(100vh - 200px);
    }
    .table_priceboard_public {
        color: #e8e8e8;
        position: relative;
        margin: 0;
        min-width: 1154px;
        width: 100%;
        border-collapse: collapse;
        background: #252525;
    }
    .table_priceboard_public>thead {
        background: #252525;
    }
    .table_priceboard_public>thead>tr>th {
        border: 1px solid #666;
        padding: 2px;
        // display: table-cell;
        font-weight: 400;
        height: 24px;
        // vertical-align: middle;
        font-size: 9.5pt;
        white-space: nowrap;
        overflow: hidden;
        border-color: #343434;
    }
    .table_priceboard_public td {
        padding: 0 2px 0 0!important;
        // line-height: 25px;
        border: 1px solid #666;
        // vertical-align: middle;
        // display: table-cell;
        white-space: nowrap;
        font-size: 9.7pt;
        overflow: hidden;
        border-bottom: unset;
        border-top: unset;
        border-color: #343434;
    }
    td {
        font-size: 12.5px;
        font-family: tahoma,helvetica,arial,sans-serif;
        text-align: left;
    }
    .table_priceboard_public>tbody tr:nth-child(odd) {
        background-color: #181818;
    }
    .table_priceboard_public>tbody tr:nth-child(even) {
        background-color: #0a0a0a;
    }
    .table-hover>tbody>tr:hover {
        background-color: #363636;
        color: unset;
    }
    .stockInfoExtent .tableStockInfo td, th {
        height: 1.3rem!important;
    }
    .tableStockInfo td, th {
        padding: 0!important;
        height: 1.3rem!important;
    }
    .divPriceBoardPublic::-webkit-scrollbar {
        width: 8px;
        height: 8px;
    }
    .divPriceBoardPublic::-webkit-scrollbar-thumb {
        background: #888787;
        border: 0 #fff;
        border-radius: 8px;
    }
    .divPriceBoardPublic::-webkit-scrollbar-track {
        background: #aaaaaa;
        border: 0 #fff;
        border-radius: 8px;
    }
    .stkNmFvl, .td_stkcd {
        text-align: center;
        background-color: rgba(89,96,95,.8);
    }
    .match-col {
        background-color: rgba(77,76,76,.8);
    }
    .load {
        color: white;
        position: fixed;
        top: 40%;
        left: 50%;
        z-index: 2000;
    }
    .custom-control {
        padding-top: 0.1rem;
    }
    .custom-control-label::before {
        top: -0.2rem;
    }
    .dropdown-menu {
        // position: absolute;
        // top: 100%;
        // left: 0;
        // z-index: 1000;
        // display: none;
        // float: left;
        // min-width: 10rem;
        // padding: .5rem 0;
        // margin: .125rem 0 0;
        font-size: 1rem;
        color: #212529;
        text-align: left;
        list-style: none;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid rgba(0,0,0,.15);
        border-radius: .25rem;
    }
    .dropdown-menu {
        padding-top: 0;
        padding-bottom: 0;
        border-radius: 0;
        font-size: 14px;
        box-shadow: 0 6px 12px rgba(0,0,0,.2);
    }
    .top-dropdown-menu {
        width: 316px;
        max-height: 250px;
        overflow: hidden;
    }
}
.modal-public {
    .card .card-header {
        background-color: #181818;
        color: #fff;
        line-height: 10px;
        border-radius: 0;
        border: none;
        padding: .75rem 1.25rem;
        margin-bottom: 0;
    }
    .card {
        border: 0;
    }
    .table-responsive a, .table-responsive th {
        color: inherit;
    }
    .table-responsive td {
        // font-size: inherit;
        font-size: 12px;
    }
    .nav-tabs {
        background-color: #9c9fa2;
        border-bottom: 1px solid #9c9fa2;
    }
    .w-100p {
        width: 100%;
    }
    .nav-tabs .nav-link.active, .nav-tabs .nav-link.active:hover {
        background: #344154;
        color: #fff;
        border: 0;
    }
    .nav-tabs .nav-item {
        margin-bottom: -3px;
        margin-top: 2px;
    }
    .nav-tabs .nav-link {
        border: 1px solid transparent;
        border-top-left-radius: .25rem;
        border-top-right-radius: .25rem;
        color: beige;
    }
    .widget-controls {
        visibility: hidden;
    }

}
.modal-content-public {
    color: #000;
    background: lightslategray;
    border: solid 1px silver;
}
.widget-controls {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    padding: 10px 20px;
    font-size: 14px;
}
.widget-controls a {
    color: #fff;
    margin-left: 7px;
    line-height: 1;
    vertical-align: top;
    display: inline-block;
}
.custom-checkbox .custom-control-input:indeterminate~.custom-control-label::before {
    border-color: unset;
    background-color: whitesmoke;
}
.custom-checkbox .custom-control-input.checkbox-warning:checked~.custom-control-label::before {
    background: #f79a17;
}
