body {
    font-family: Open Sans, sans-serif;
    font-style: normal;
    font-weight: normal;
    // font-size: 14px;
    font-size: 12px;
    line-height: 140%;
}

.modal-xxl {
    font-size: 12px;
    font-family: tahoma, helvetica, arial, sans-serif;
    .form-group {
        margin-bottom: 5px;
    }
    .form-group .form-control {
        // font-size: 11pt;
        white-space: nowrap;
        background: inherit;
        color: inherit;
    }
    label:not(.marginAuto) {
        margin-bottom: 0;
        white-space: nowrap;
    }
    .nowrap {
        white-space: nowrap;
    }
    .text-center {
        text-align: center !important;
    }
    .text-right {
        text-align: right !important;
    }
    .text-left {
        text-align: left !important;
    }
    .text-append {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
}

.main-content section.content {
    overflow: unset;
}

.main-div-contain {
    height: 100%;
    min-width: 990px;
}

.w-widgetbar-handle {
    clear: both;
    position: absolute;
    background: #949c95;
    height: 12px;
    cursor: ns-resize;
    width: 100%;
    z-index: 35;
    opacity: 0;
}
// .w-widgetbar-handle:hover {
//   // opacity: 1;
// }

.h-widgetbar-handle {
    position: absolute;
    width: 1px;
    background: #949c95;
    // cursor: ew-resize;
    z-index: 35;
    opacity: 0;
    // display: none;
}
.h-widgetbar-handle.rightFix {
    cursor: ew-resize;
}
// .h-widgetbar-handle:hover {
//   // opacity: 1;
// }

.left-widgetbar-handle {
    height: calc(100% - 70px);
    top: 70px;
    left: 205px;
}

.right-child-widgetbar-handle {
    height: 100%;
    right: -25px;
    width: 12px;
}

.div_left_elm {
    clear: both;
    position: absolute;
    // top: 140px;
    // top: 110px;
    top: 92px;
    left: 10px;
    bottom: 0;
    overflow: visible;
    // width: 205px;
    width: 215px;
    background-color: #3c3c3c;
    color: #eeeeee;
    // padding-bottom: 0.5px;
    // z-index: 100;
    // overflow-y: auto;
}

.div_right_elm {
    // height: calc(70% - 130px);
    height: calc(70% - 112px);
    right: 10px;
    clear: both;
    position: absolute;
    // top: 140px;
    // top: 110px;
    top: 92px;
    // width: calc(100% - 249px);
    width: calc(100% - 244px);
    z-index: 32;
    background: #242424;
}

.div_right_l_elm {
    height: 100%;
    width: 100%;
    float: left;
    overflow: auto;
    border-top: 0.2px solid #8e877c;
}

.div_right_r_elm {
    height: 100%;
    background: #3c3c3c;
    width: 0.01px;
    float: right;
    position: absolute;
    top: 0;
    right: 0;
    overflow: auto;
    border-top: 0.2px solid #8e877c;
    // padding: 0.5px 0 0 1px; // -- Cho đường kẻ dọc
}

.bottom-widgetbar-handle {
    bottom: calc(31% - 3px);
    // width: calc(100% - 249px);
    width: calc(100% - 244px);
    right: 10px;
}

.div_bottom_elm {
    bottom: 5px;
    right: 10px;
    height: calc(30%);
    position: absolute;
    background: black;
    // width: calc(100% - 249px);
    width: calc(100% - 244px);
    z-index: 32;
    // overflow: hidden;
    // padding-left: 2px; // -- Cho đường kẻ dọc
}

.close_div {
    display: block;
    color: #f0c518;
    text-align: center;
    line-height: 15px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    -webkit-transform: translate3d(0, 0, 0);
}

.sideBar_close_left {
    border-radius: 30% 0 0 30%;
    float: right;
    width: 15px;
    height: 20px;
    padding: 2px 0;
    // margin-top: 250px;
}

.sideBar_close_right {
    border-radius: 0 30% 30% 0;
    float: left;
    width: 15px;
    height: 20px;
    padding: 0px 0px 0px 3px;
    // margin: 250px auto;
}

.showClass {
    display: block !important;
}

.hideClass {
    display: none !important;
}

.child_close_right {
    border-radius: 0 30% 30% 0;
    float: left;
    bottom: 270px;
    width: 15px;
    height: 20px;
    position: absolute;
    padding: 2px 0;
}

.bottom_close_down {
    border-radius: 0 0 30% 30%;
    float: right;
    right: 10px;
    width: 20px;
    height: 15px;
    position: absolute;
    padding: 5px 2px 0px 0px;
}

.bottom_close_up {
    border-radius: 30% 30% 0 0;
    float: right;
    right: 0;
    width: 20px;
    height: 15px;
    position: absolute;
    bottom: 3px;
    padding: 0 2px;
}

