@-webkit-keyframes warning {
    0% {
        color: yellow;
    }
    30% {
        color: yellow;
    }
    90% {
        color: '#EF9A9A';
    }
    100% {
        color: green;
    }
}
@-moz-keyframes warning {
    0% {
        color: yellow;
    }
    30% {
        color: yellow;
    }
    90% {
        color: '#EF9A9A';
    }
    100% {
        color: green;
    }
}
@-o-keyframes warning {
    0% {
        color: yellow;
    }
    30% {
        color: yellow;
    }
    90% {
        color: '#EF9A9A';
    }
    100% {
        color: green;
    }
}
@keyframes warning {
    0% {
        color: yellow;
    }
    30% {
        color: yellow;
    }
    90% {
        color: '#EF9A9A';
    }
    100% {
        color: green;
    }
}
.wifiWarning {
    -webkit-animation: warning 2000ms infinite;
    -moz-animation: warning 2000ms infinite;
    -o-animation: warning 2000ms infinite;
    animation: warning 2000ms infinite;
}

