.row-sellbuy {
    box-sizing: border-box;
    flex: 0 0 auto;
}
.sideControl-sellbuy {
    display: flex;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 100%;
    box-sizing: border-box;
}
.section-sellbuy.active {
    color: #d1d4dc;
}
.section-sellbuy.sell-attr {
    // border-radius: 4px 0 0 4px;
    // padding: 5px 26px 7px 8px;
    // text-align: left;
    
    border-radius: 0px 4px 4px 0px;
    padding: 5px 26px 0 8px;
    text-align: right;
}
.sell-attr.active {
    background-color: #f05050;
}
.buy-attr.active {
    background-color: #1976d2;
}
.section-order-match {
    position: absolute;
    border-radius: 2px;
    width: 32px;
    height: 14px;
    // background-color: #303030;
    background-color: #16151b;
    left: 128px;
    top: 15px;
    font-size: 9px;
    font-weight: 600;
    text-align: center;
    // padding-top: 2px;
}
.title-sellbuy {
    flex: 1 1;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    text-align: center;
    font-weight: bold;
}
.value-sellbuy {
    flex: 1 1;
    font-size: 12px;
    line-height: 16px;
}
.sell-attr .value-sellbuy {
    text-align: left;
    margin-top: 7px;
}
.buy-attr .value-sellbuy {
    text-align: right;
    margin-top: 7px;
}
.section-sellbuy.buy-attr {
    // border-radius: 0 4px 4px 0;
    // padding: 5px 8px 7px 26px;
    // text-align: right;

    border-radius: 4px 0 0 4px;
    padding: 5px 8px 0px 26px;
    text-align: left;
}
.section-sellbuy {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    min-height: 40px;
    transition: background-color .35s ease,color .35s ease;
    color: #50535e;
    background-color: #37393e;
}
.section-sellbuy.sell-attr {
    border-left: 1px solid #303030;
}
.section-sellbuy.buy-attr {
    border-right: 1px solid #303030;
}

.widgetbar-widgetheader {
    background: #37393e;
    height: 34px;
    cursor: default;
    // margin: 3px;
    box-sizing: border-box;
    contain: strict;
    border-radius: 2px;
}
.widgetbar-widgetbody {
    position: relative;
    overflow: hidden;
    contain: strict;
}
.widgetbar-headerspace {
    position: relative;
    float: right;
    overflow: hidden;
    padding: 5px 10px 0 0;
    font-size: 16px;
}
html .widgetbar-headerspace .button {
    color: #758696;
    border-color: #363c4e;
    background-color: #131722;
}
.widgetbar-headerspace .button {
    display: block;
    position: relative;
    float: left;
    padding-top: 5px;
    background-color: #fff;
    border: 1px solid;
    border-color: #dadde0;
    font-size: 12px;
    line-height: 15px;
    width: 39px;
    height: 30px;
    text-align: center;
    box-sizing: border-box;
    margin-left: 4px;
    color: #7d7d7d;
    fill: currentColor;
}
.widgetbar-widgettitle {
    padding: 10px 0 9px 0px;
    white-space: nowrap;
    font-size: 13px;
    line-height: 15px;
    font-weight: bold;
    color: #4f5966;
}
.widgetbar-widgettitle {
    overflow: hidden;
    text-overflow: ellipsis;
}
.orderForm .widgetbar-widgettitle {
    color: gray;
}
.tv-news {
    height: 100%;
    position: relative;
    overflow: hidden;
}
.ns-data {
    position: absolute;
    width: 100%;
    transition: opacity .3s;
}
.orderForm .ns-item {
    border-bottom-color: #363c4e;
}
.ns-item {
    border-bottom: 1px solid;
    border-bottom-color: #dadde0;
    padding: 2px 0;
    position: relative;
}
.orderForm .ns-item-title {
    color: #c5cbce;
}
.ns-item-title {
    margin: 0 5px;
    line-height: 120%;
    color: #4a4a4a;
    a {
        color: #c0c0c0;
    }
}

.ns-item-content {
    color: #7d7d7d;
    margin: 2px 12px;
    line-height: 120%;
    font-size: 12px;
}
.orderForm .ns-item-content {
    // font-size: 10px;
    margin: 2px 5px;
    min-height: 72px;
}
.ns-item-desc {
    margin: 2px 30px 0 12px;
    min-height: 13px;
}
.orderForm .ns-item-desc {
    margin: 2px 30px 0 4px;
    width: 150px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.ns-item-date, .ns-item-desc {
    color: #8f8f8f;
    font-size: 10px;
}
.ns-item-date {
    position: absolute;
    bottom: 2px;
    right: 12px;
}
.ns-item-date, .ns-item-desc {
    color: #8f8f8f;
    font-size: 10px;
}


