.react-tiny-popover-container {
    z-index: 99999;
    overflow: unset !important;
}
.popover-search {
    max-width: 350px;
    background: #403d4f;
    color: white;
    padding: 12px;
    td {
        font-size: 12px;
    }
    td input[type='radio'] {
        position: absolute;
        visibility: hidden;
    }

    td label {
        display: block;
        position: relative;
        padding: 11px 15px 15px 25px;
        height: 30px;
        z-index: 9;
        cursor: pointer;
        -webkit-transition: all 0.25s linear;
        color: #aaaaaa;
        white-space: nowrap;
    }

    td:hover label {
        color: #ffffff;
    }

    td .check {
        display: block;
        border: 2px solid #aaaaaa;
        border-radius: 100%;
        height: 16px;
        width: 16px;
        margin-top: -27px;
        z-index: 5;
        transition: border 0.25s linear;
        -webkit-transition: border 0.25s linear;
    }

    td:hover .check {
        border: 3px solid #ffffff;
    }

    td .check::before {
        display: block;
        content: '';
        border-radius: 100%;
        height: 8px;
        width: 8px;
        margin: 2px;
        transition: background 0.25s linear;
        -webkit-transition: background 0.25s linear;
    }

    input[type='radio']:checked ~ .check {
        border: 2px solid #0dff92;
    }

    input[type='radio']:checked ~ .check::before {
        background: #0dff92;
    }

    input[type='radio']:checked ~ label {
        color: #0dff92;
    }
}
