.table_sticky_market_index {
    thead tr th:first-child {
        position: -webkit-sticky;
        position: sticky;
        left: 0px;
        width: 65px;
        z-index: 4;
    }
    thead tr th:nth-child(2) {
        position: -webkit-sticky;
        position: sticky;
        left: 65px;
        width: 50px;
        z-index: 4;
    }
    thead tr th:nth-child(3) {
        position: -webkit-sticky;
        position: sticky;
        left: 115px;
        // width: 38px;
        z-index: 4;
    }
    thead tr th:nth-child(4) {
        position: -webkit-sticky;
        position: sticky;
        left: 153px;
        // width: 38px;
        z-index: 4;
    }
    thead tr:first-child th {
        position: -webkit-sticky;
        position: sticky;
        background-color: #080024;
        color: #d3d3d3;
        top: -1px;
        z-index: 3;
    }

    tbody tr td:first-child {
        position: -webkit-sticky;
        position: sticky;
        left: 0px;
        width: 65px;
        background: #36324a;
        // border-bottom: 1px solid #5f575770;
    }
    tbody tr td:nth-child(2) {
        position: -webkit-sticky;
        position: sticky;
        left: 65px;
        width: 50px;
        z-index: 2;
        background: #36324a;
    }
    tbody tr td:nth-child(3) {
        position: -webkit-sticky;
        position: sticky;
        left: 115px;
        // width: 38px;
        z-index: 2;
        background: #36324a;
    }
    tbody tr td:nth-child(4) {
        position: -webkit-sticky;
        position: sticky;
        left: 153px;
        // width: 38px;
        z-index: 2;
        background: #36324a;
    }
}
